/**
 * ar (Arabic)
 */
export const ar = function(number, index) {
  if (index === 0) {
    return [' ', ' '];
  }

  const timeTypes = [
    ['', '', '%s ', '%s '],    // Seconds
    ['', '', '%s ', '%s '],   // Minutes
    ['', '', '%s ', '%s '],      // Hours
    ['', '', '%s ', '%s '],         // Days
    ['', '', '%s ', '%s '], // Weeks
    ['', '', '%s ', '%s '],         // Months
    ['', '', '%s ', '%s '],        // Years
  ];

  const timeStr = formatTime(Math.floor(index / 2), number);

  return [`  ${timeStr}`, `  ${timeStr}`];

  function formatTime(type, n) {
    if (n < 3) {
      return timeTypes[type][n - 1];
    }
    else if (n >= 3 && n <= 10) {
      return timeTypes[type][2];
    }
    else {
      return timeTypes[type][3];
    }
  }
};

/**
 * be (Belarusian)
 */
export const be = function(number, index) {
  const seconds =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');
  const minutes =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');
  const hours =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');
  const days =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');
  const weeks =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');
  const months =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');
  const years =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');

  switch (index) {
    case 0: return [' ', '  '];
    case 1: return [seconds(number) + ' ', ' ' + seconds(number)];
    case 2:
    case 3: return [minutes(number) + ' ', ' ' + minutes(number)];
    case 4:
    case 5: return [hours(number) + ' ', ' ' + hours(number)];
    case 6:
    case 7: return [days(number) + ' ', ' ' + days(number)];
    case 8:
    case 9: return [weeks(number) + ' ', ' ' + weeks(number)];
    case 10:
    case 11: return [months(number) + ' ', ' ' + months(number)];
    case 12:
    case 13: return [years(number) + ' ', ' ' + years(number)];
    default: return ['', ''];
  }

  /**
   *
   * @param f1 - 1
   * @param f - 21, 31, ...
   * @param s - 2-4, 22-24, 32-34 ...
   * @param t - 5-20, 25-30, ...
   * @param n
   * @returns {string}
   */
  function formatNum(f1, f, s, t, n) {
    const n10 = n % 10;
    let str = t;

    if (n === 1) {
      str = f1;
    } else if (n10 === 1 && n > 20) {
      str = f;
    } else if (n10 > 1 && n10 < 5 && (n > 20 || n < 10)) {
      str = s;
    }

    return str;
  }
};

