/**
 * ro (Romanian)
 */
export const ro = function(number, index) {
  const langTable = [
    ['chiar acum', 'chiar acum'],
    ['acum %s secunde', 'peste %s secunde'],
    ['acum un minut', 'peste un minut'],
    ['acum %s minute', 'peste %s minute'],
    ['acum o or', 'peste o or'],
    ['acum %s ore', 'peste %s ore'],
    ['acum o zi', 'peste o zi'],
    ['acum %s zile', 'peste %s zile'],
    ['acum o sptmn', 'peste o sptmn'],
    ['acum %s sptmni', 'peste %s sptmni'],
    ['acum o lun', 'peste o lun'],
    ['acum %s luni', 'peste %s luni'],
    ['acum un an', 'peste un an'],
    ['acum %s ani', 'peste %s ani'],
  ];

  if (number < 20) {
    return langTable[index];
  }

  // A `de` preposition must be added between the number and the adverb
  // if the number is greater than 20.
  return [
    langTable[index][0].replace('%s', '%s de'),
    langTable[index][1].replace('%s', '%s de'),
  ];
};

/**
 * ru (Russian)
 */
export const ru = function(number, index) {
  const seconds =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');
  const minutes =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');
  const hours =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');
  const days =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');
  const weeks =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');
  const months =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');
  const years =
    formatNum.bind(null, '', '%s ', '%s ', '%s ');

  switch (index) {
    case 0: return [' ', '  '];
    case 1: return [seconds(number) + ' ', ' ' + seconds(number)];
    case 2: // [' ', ' '];
    case 3: return [minutes(number) + ' ', ' ' + minutes(number)];
    case 4: // [' ', ' '];
    case 5: return [hours(number) + ' ', ' ' + hours(number)];
    case 6: return ['', ''];
    case 7: return [days(number) + ' ', ' ' + days(number)];
    case 8: // [' ', ' '];
    case 9: return [weeks(number) + ' ', ' ' + weeks(number)];
    case 10: // [' ', ' '];
    case 11: return [months(number) + ' ', ' ' + months(number)];
    case 12: // [' ', ' '];
    case 13: return [years(number) + ' ', ' ' + years(number)];
    default: return ['', ''];
  }

  /**
   *
   * @param f1 - 1
   * @param f - 21, 31, ...
   * @param s - 2-4, 22-24, 32-34 ...
   * @param t - 5-20, 25-30, ...
   * @param n
   * @returns {string}
   */
  function formatNum(f1, f, s, t, n) {
    const n10 = n % 10;
    let str = t;

    if (n === 1) {
      str = f1;
    } else if (n10 === 1 && n > 20) {
      str = f;
    } else if (n10 > 1 && n10 < 5 && (n > 20 || n < 10)) {
      str = s;
    }
    return str;
  }
};

